@import url(http://fonts.cdnfonts.com/css/doge-sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.chat {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #1A1A1D;
}

.leftContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #FFFFFF;
    border-radius: 12px;
    height: 90%;
    width: 70%;
    padding-left: 1%;

}
.message_form {
    display: flex;
}

.userListContainer {
    display: flex;
    align-items: center;
    height: 60px;
    width: 10%;
    padding-left: 2%;
}
.app {
  display: flex;
  height: 100%;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: system-ui;
  font-weight: 600;
  color: 	#e65a48; 
}

.header {
  padding: 20px;
  text-align: center;
  background: #1abc9c;
  color: white;
  font-size: 40px;
  font-family: 'Doge Sans', sans-serif;
}

.send-message {
  grid-area: s;
}

.messageListContainer {
  grid-area: m;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html, body {
    font-family: sans-serif;
    /* padding: 0;
    margin: 0; */
}


.loginBody {
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    height: 100vh;
    background-color: #e74c3c;
}

.loginContainer {
    width: 60%;
}

* {
    box-sizing: border-box;
}

.loginTitle {
    color: white;
    font-size: 4em;
    padding-bottom: 10px;
}

.loginForm {
    width: 50%;
    margin: auto;
}

.loginInput {
    border-radius: 0;
    padding: 15px 20px;
    width: 100%;
    font-size: 1em;
}

.submit {
    visibility: hidden;
}
.messageListContainer {
    overflow: auto;
    flex: auto;
    height: 60vh;
}
