html, body {
    font-family: sans-serif;
    /* padding: 0;
    margin: 0; */
}


.loginBody {
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    height: 100vh;
    background-color: #e74c3c;
}

.loginContainer {
    width: 60%;
}

* {
    box-sizing: border-box;
}

.loginTitle {
    color: white;
    font-size: 4em;
    padding-bottom: 10px;
}

.loginForm {
    width: 50%;
    margin: auto;
}

.loginInput {
    border-radius: 0;
    padding: 15px 20px;
    width: 100%;
    font-size: 1em;
}

.submit {
    visibility: hidden;
}